@charset "utf-8";

// Define defaults for each variable.
$base-font-family: "PT Serif", -apple-system, BlinkMacSystemFont, "Segoe UI", "Noto Sans", "Helvetica Neue", Arial, sans-serif, "Apple Color Emoji", "Segoe UI Emoji", "Segoe UI Symbol", "Noto Color Emoji" !default;
$base-font-size:   16px !default;
$base-font-weight: 400 !default;

$name-font-family: "Montserrat", sans-serif;
$title-font-family: "Quicksand", sans-serif;

$small-font-size:  $base-font-size * 0.875 !default; // 14px
$middle-font-size: $base-font-size * 1.125 !default; // 18px
$large-font-size:  $base-font-size * 1.25  !default; // 20px
$huge-font-size:   $base-font-size * 2     !default; // 32px
$massive-font-size: $base-font-size * 2.25     !default; // 36px

$base-line-height: 1.65 !default;

$spacing-unit:     30px !default;

// Width of the content area
$content-width:    1000px !default;

// Default color settings
$text-color:        #2b3d4f;
$text-second-color: #354a5f;
$text-third-color:  #483f36;
$background-color:  #e5e5e5;
$wrapper-color:     #f9f9f9;
$heart-color:       #bf3a2b;
$theme-color:       #477cc1;
$text-name-color:   #202020;

$item-delimiter: "Â·";

@import
  "sanitize",
  "base",
  "responsive",
  "print",
  "fonts"
;

section {
  .section-title {
    text-transform: uppercase;
    justify-content: start;
    border-bottom-style: dashed;

    

    
  }

  
  .section-content {
    .markdown, .items {
      a::after {
        display: inline-block;
        text-rendering: auto;
        -webkit-font-smoothing: antialiased;
        font: var(--fa-font-solid);
        content: "\f08e";
        color: $theme-color;
        font-size: 0.625em;
        margin: 0 0.375em;
        text-decoration: none;
        vertical-align: middle;
      }
    }
  }
  
}
